<template>
  <section class="cmn-block">
    <div class="cmn-header">
      <img src="@/assets/img/icn/logo.svg" alt="logotype" class="logo" />
      <h1 class="title">{{ title }}</h1>
    </div>
    <form action="" class="cmn-form">
      <p class="txt txt-form txt-header" v-html="textForm"></p>
      <ul class="inputs-list" v-for="item in inputsField" :key="item.id">
        <p class="txt txt-form cmn-label">{{ item.labelText }}</p>
        <div class="slot-wrap" :class="{ full: item.aFull }">
          <input
            :type="item.typeInp"
            class="cmn-input"
            :placeholder="item.plholder"
            :class="{ 'checked-input': item.aFull }"
            v-model="item.aFull"
          />
          <img
            class="check-img"
            src="../assets/img/icn/check.svg"
            alt="It's cool!"
            v-show="item.aFull"
          />
        </div>
      </ul>
    </form>
    <p class="txt txt-footer" v-html="textFooter"></p>
    <slot name="footerBtn"></slot>
  </section>
</template>

<script>
export default {
  name: "cmnForm",
  props: {
    title: {
      type: String,
      required: false,
    },
    textForm: {
      type: String,
      required: true,
    },
    inputsField: {
      type: Object,
      required: true,
    },
    textFooter: {
      type: String,
      required: true,
    },
  },
};
</script>
