<template>
    <button class="cmn-btn" >
          {{textBtn}}
          <img :src="btnImgPath" v-if="Boolean(btnImgPath)"/>
        </button>
</template>

<script>
    export default {
        name: 'cmnBtn',
        props: {
            textBtn: {
                type: String,
                required: true
            },
            btnImgPath: {
                type: String,
                required: true,
            }
        }
    }
</script>